// slider intro
// -------------------------------------------
const sliderItem = document.querySelector('.intro__slider-list').querySelector('.intro__slider-items');
const sliderItemWidth = sliderItem.offsetWidth;
const dot = document.querySelector('.intro__slider-dot')
const prevArrow = document.querySelector('.intro__slider-arrows--left');
const nextArrow = document.querySelector('.intro__slider-arrows--right');
let activeItem = 0;
const images = ['./img/intro1.png',
  './img/intro2.png',
  './img/intro3.png']

prevArrow.addEventListener('click', (event) => {
  if (activeItem - 1 == -1) {
    activeItem = images.length
  }
  let linkImg = images[--activeItem];
  moveDots(activeItem);
  sliderItem.style.background = `url(${linkImg}) center center / cover no-repeat`;
});
nextArrow.addEventListener('click', (event) => {
  if (activeItem + 1 == images.length) {
    activeItem = -1
  }
  let linkImg = images[++activeItem];
  moveDots(activeItem);
  sliderItem.style.background = `url(${linkImg}) center center / cover no-repeat`;
});
//move dot slider
let moveDots = (activeItem) => {
  dot.style.left = ((sliderItemWidth / images.length) * activeItem + 'px');
}


//catalog
// -------------------------------------------
const catalogMenu = document.querySelector('.catalog__menu');
const catalogItems = catalogMenu.querySelectorAll('.catalog__items')
const catalogList = document.querySelector('.catalog__catalog')
let activeCatalogItems = 1;
for (let item of catalogItems) {
  item.addEventListener('click', () => {
    // menu
    const prevItem = catalogMenu.querySelector('#catalog__item-' + activeCatalogItems);
    prevItem.classList.remove('catalog__items--active');
    item.classList.add('catalog__items--active');
    // list
    const prevListItems = catalogList.querySelector('#catalog__catalog-' + activeCatalogItems);
    activeCatalogItems = item.id[item.id.length - 1]
    const nextListItems = catalogList.querySelector('#catalog__catalog-' + activeCatalogItems);
    prevListItems.classList.remove('catalog__list--active');
    nextListItems.classList.add('catalog__list--active');
  });
}


//services
// -------------------------------------------
const servicesItems = document.querySelectorAll('.services__items');
for (let item of servicesItems) {
  item.addEventListener('click', (event) => {
    if (event.target.className != "services__text") {
      const accordion = item.querySelector('.services__accordion');
      const servicesTextWidth = item.querySelector('.services__text').offsetHeight;
      if (accordion.style.maxHeight == "0px" || accordion.style.maxHeight.length == 0) {
        accordion.style.maxHeight = servicesTextWidth + 'px';
        item.classList.add('services__items--active');
      } else {
        accordion.style.maxHeight = '0px';
        item.classList.remove('services__items--active');
      }
    }
  });
}


//burger
// -------------------------------------------
const burger = document.querySelector('.burger');
const body = document.querySelector('.body');

burger.addEventListener('click', (e) => {
  if (e.target.className == 'burger') {
    burger.classList.add('burger--active');
    body.classList.add('body--lock')
  } else if (e.target.className =='burger__close') {
    burger.classList.remove('burger--active');
    body.classList.remove('body--lock')
  }

});
